.test{
    height: 600px;
    width: 80%;
    overflow:hidden;
    margin:0 auto;
    
}
.CarouselImg{
    height: 600px;
    width: 100%;
}
@media screen and (max-width:960px){
    .test{
        width: 100%;
    }  
    .CarouselImg{
        width: 100%;
    } 
} 
@media screen and (max-width:768px){
    .test{
        height:500px;
    }  
    .CarouselImg{
        height:500px;
    } 
} 
@media screen and (max-width:480px){
    .test{
        height:330px;
    }  
    .CarouselImg{
        height:330px;
    } 
} 